import App from './App.svelte';
import words from './words.js';
let cDate = new Date();
let cYear = cDate.getFullYear();
let cMonth = cDate.getMonth() + 1;
let cDay = cDate.getDate();
let dateString = "" + cYear + "" + cMonth + "" + cDay;
let dateNum = parseInt(dateString);
let word = words[dateNum % words.length];
const app = new App({
    target: document.body,
    props: {
        name: word
    }
});
export default app;
